import type { EChartsOption } from 'echarts'
import { useEcharts } from '@/hooks/useEcharts'

interface IChartProp {
  value: string;
  name: string;
  percentage: string;
}

const AgeRatioChart = () => {
  const data: IChartProp[] = [
    {
      value: '200',
      name: "10岁以下",
      percentage: "16%"
    },
    {
      value: '110',
      name: "10 - 18岁",
      percentage: "8%"
    },
    {
      value: '150',
      name: "18 - 30岁",
      percentage: "12%"
    },
    {
      value: '310',
      name: "30 - 40岁",
      percentage: "24%"
    },
    {
      value: '250',
      name: "40 - 60岁",
      percentage: "20%"
    },
    {
      value: '260',
      name: "60岁以上",
      percentage: "20%"
    }
  ]

  const colors = ["#F6C95C", "#EF7D33", "#1F9393", "#184EA1", "#81C8EF", "#9270CA"]

  const option: EChartsOption = {
    color: colors,
    tooltip: {
      show: true,
      trigger: "item",
      formatter: "{b} <br/>占比：{d}%"
    },
    legend: {
      orient: "vertical",
      right: "20px",
      top: "15px",
      itemGap: 15,
      itemWidth: 14,
      formatter: (name) => {
        let text = ""
        data.forEach((val: IChartProp) => {
          if (val.name === name) {
            text = " " + name + "　 " + val.percentage
          }
        })
        return text
      },
      textStyle: {
        color: "#fff"
      }
    },
    grid: {
      top: "bottom",
      left: 10,
      bottom: 10
    },
    series: [
      {
        zlevel: 1,
        name: "年龄比例",
        type: "pie",
        selectedMode: "single",
        radius: [50, 90],
        center: ["35%", "50%"],
        startAngle: 60,
        label: {
          position: "inside",
          show: true,
          color: "#fff",
          formatter: (params) => {
            return (params.data as IChartProp)?.percentage
          },
          rich: {
            b: {
              fontSize: 16,
              lineHeight: 30,
              color: "#fff"
            }
          }
        },
        itemStyle: {
          shadowColor: "rgba(0, 0, 0, 0.2)",
          shadowBlur: 10
        },
        data: data.map((val, index) => {
          return {
            value: Number(val.value),
            name: val.name,
            percentage: val.percentage,
            itemStyle: {
              borderWidth: 10,
              shadowBlur: 20,
              borderColor: colors[index],
              borderRadius: 10
            }
          }
        })
      },
      {
        name: "",
        type: "pie",
        selectedMode: "single",
        radius: [50, 90],
        center: ["35%", "50%"],
        startAngle: 60,
        data: [
          {
            value: 1000,
            name: "",
            label: {
              show: true,
              formatter: "{a|本日总数}",
              rich: {
                a: {
                  align: "center",
                  color: "rgb(98,137,169)",
                  fontSize: 14
                }
              },
              position: "center"
            }
          }
        ]
      }
    ]
  }

  const [echartsRef] = useEcharts(option, data)
  return <div ref={echartsRef} style={{ width: "100%", height: "100%" }}></div>
}

export default AgeRatioChart
